body {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: sans-serif;
  margin: 0;
  padding: 0; }

h1 {
  text-align: center; }

.postWrapper {
  position: relative;
  height: 1000px;
  width: 100%; }
  .postWrapper .posts {
    left: 5%;
    list-style-type: none;
    padding: 0;
    position: absolute;
    width: 90%; }
    .postWrapper .posts li {
      background: #d2f3f4;
      border: 1px solid gray;
      border-radius: 10px;
      margin: 5px 0;
      padding: 10px; }

form {
  position: relative;
  width: 100%;
  height: 200px;
  text-align: center; }
  form .postText {
    text-align: center;
    height: 150px;
    width: 50%; }
  form .postSubmit {
    height: 150px;
    width: 50%; }



